import React, {Component} from 'react';
import '../../styles/Home.css';

class Home extends Component {
    state = {
        header: "Undergraduate Software Engineer",
        name: "ZIAD HISHAM ALI",
    }

    hireMe = (e) => {
        e.preventDefault();
        console.log("hire me clicked");
        document.getElementById("contact-area").classList.toggle("moveContactLeft");
    }
    
    render() {
        return (
            <div className="home section parallax bg1">
                <span className="header forte">{this.state.header}</span>
                <span className="name">{this.state.name}<span className="orange-text">.</span></span>
                <button className="submit-button margin-top-- box-shadow" onClick={e => this.hireMe(e)}>HIRE ME</button>
            </div>
        );
    }
}

export default Home